@import "../../global.scss";

.topbar {
  width: 100%;
  height: 70px;
  background-color: rgb(20,22,40); //navbar white
  color: $mainColor;
  position: fixed;
  top: 0;
  z-index: 3;
  transition: all 1s ease;

  .wrapper {
    padding: 10px 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .left {
      display: flex;
      align-items: center;
      .logo {
        font-family: 'Niconne', cursive;

        font-size: 40px;
        text-decoration: none;
        color: inherit;
        margin-right: 40px;

        span{
          color: rgb(253, 67, 112);
          font-family: 'Niconne', cursive;

        }
        
      }
      
    }
    .right {
      .hamburger {
        width: 32px;
        height: 25px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        cursor: pointer;

        span {
          width: 100%;
          height: 3px;
          background-color: $mainColor;
          transform-origin: left;
          transition: all 2s ease;

        }
      }
    }
  }
  &.active {
    background-color: black;
    color: rgb(255, 255, 255);
    
    .hamburger {
      span {
        &:first-child {
          background-color: rgb(253, 67, 112);
          transform: rotate(45deg);

        }
        &:nth-child(2) {
          opacity: 0;
        }
        &:last-child {
          background-color: rgb(253, 67, 112);
          transform: rotate(-45deg);

        }
      }
  }
}
}